<template>
    <div class="team">
        <div class="head">
            <div class="left">
                <p>{{ lang.title }} ({{ count }}) </p>
                <p></p>
            </div>
            <div class="right">
                <el-button type="primary" size="small" icon="el-icon-plus" @click="showAdd = true">{{ lang.add
                }}</el-button>
            </div>
        </div>
        <div class="table_wrapper">
            <el-table class="table" border :data="list" size="small" v-loading="loading" height="100%">
                <el-table-column prop="id" label="ID" width="70"></el-table-column>
                <el-table-column :label="lang.table.nick" width="160">
                    <template slot-scope="scope">
                        <img :src="scope.row.avatar">
                        <span>{{ scope.row.user_name }}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="create_time" :label="lang.table.regTime" width="120"></el-table-column>
                <el-table-column prop="last_time" :label="lang.table.lastTime"></el-table-column>
                <el-table-column :label="lang.table.role" width="150">
                    <template slot-scope="scope">
                        <div class="role-container">
                            <span class="role-text">{{ scope.row.role }}</span> &nbsp;
                            <i class="el-icon-edit role-icon" @click="editRoleConfirmation(scope.row)"></i>
                        </div>
                    </template>
                </el-table-column>

                <el-table-column prop="statusText" :label="lang.table.status" width="80"></el-table-column>

                <el-table-column :label="lang.table.subscription" width="150">
                    <template slot-scope="scope">
                        <span v-if="scope.row.subscription === 'Free Plan'" style="display: flex; align-items: center;">
                            <el-select v-model="scope.row.subscription" placeholder="Select subscription"
                                @change="handleSubscriptionChange(scope.row)" style="margin-right: 5px;">
                                <el-option label="Free Plan" value="Free Plan"></el-option>
                                <el-option label="Pro Plan" value="Pro Plan"></el-option>
                                <el-option label="Advance Plan" value="Advance Plan"></el-option>
                            </el-select>
                            <i class="el-icon-plus" @click="handleSubscriptionClick(scope.row)"></i>

                            <tr v-for="row in result" :key="row.id">
                                <td>{{ row.id }}</td>
                                <td>{{ row.associatedProjectsCount }}</td>
                            </tr>
                        </span>
                        <span v-else>
                            <el-select v-model="scope.row.subscription" placeholder="Select subscription"
                                @change="handleSubscriptionChange(scope.row)" style="margin-right: 5px;">
                                <el-option label="Free Plan" value="Free Plan"></el-option>
                                <el-option label="Pro Plan" value="Pro Plan"></el-option>
                                <el-option label="Advance Plan" value="Advance Plan"></el-option>
                            </el-select>
                        </span>
                    </template>
                </el-table-column>

                <el-table-column :label="lang.table.projectused" width="100">
                    <template slot-scope="scope">
                        <span v-if="scope.row.subscription === 'Pro Plan'">
                            {{ scope.row.associatedProjectsCount }}/{{ scope.row.max_projects }} projects
                            <i class="el-icon-edit" @click="editMaxProjects(scope.row)"></i>
                        </span>
                        <span v-else>
                            {{ scope.row.associatedProjectsCount }}/{{ scope.row.max_projects }} projects
                        </span>
                    </template>
                </el-table-column>

                <el-table-column :label="lang.table.dost" width="120">
                    <template slot-scope="scope">
                        <el-tooltip v-if="scope.row.status == 1" class="item" effect="dark" :content="lang.tips9"
                            placement="top">
                            <el-button type="text" icon="el-icon-unlock"
                                @click="showLockConfirmation(scope.row.id, 0)"></el-button>
                        </el-tooltip>
                        <el-tooltip v-if="scope.row.status == 0" class="item" effect="dark" :content="lang.tips10"
                            placement="top">
                            <el-button type="text" icon="el-icon-lock" @click="showLockConfirmation(scope.row.id, 1)"
                                style="border: 1px solid black; padding: 8px; border-radius: 8px;color:black;"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" :content="lang.tips8" placement="top">
                            <el-button type="text" icon="el-icon-delete" @click="showDeleteConfirmation(scope.row.id)"
                                style="background-color: red; padding: 8px; border-radius: 8px;color:white;"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="pagelist">
            <el-pagination background :page-size="150" layout=""></el-pagination>
        </div>

        <el-dialog title="Edit Role" :visible.sync="editRoleDialogVisible" width="30%" @close="cancelEditRole">
            <el-form ref="editRoleForm" :model="editRoleForm" label-width="80px">
                <el-form-item label="Role">
                    <el-select v-model="editRoleForm.role" placeholder="Select Role">
                        <el-option v-for="role in roles" :key="role" :label="role" :value="role"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="cancelEditRole">Cancel</el-button>
                <el-button type="primary" @click="saveEditRole">Save</el-button>
            </div>
        </el-dialog>

        <el-dialog :title="lang.tips11" append-to-body :visible.sync="showAdd" width="500px" :show-close="false"
            :before-close="() => { }">
            <el-form :model="addForm" label-width="80px" size="small">
                <el-form-item :label="lang.tips12">
                    <el-input v-model="addForm.name" :placeholder="lang.tips13" style="width: 360px"></el-input>
                </el-form-item>
                <el-form-item :label="lang.tips14">
                    <el-select v-model="addForm.role" :placeholder="lang.tips15" style="width: 360px">
                        <el-option :label="lang.tips16" value="2"></el-option>
                        <el-option :label="lang.tips17" value="1"></el-option>
                        <el-option :label="lang.tips18" value="0"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="cancelAdd">{{ lang.tips19 }}</el-button>
                <el-button size="small" type="primary" @click="add">{{ lang.tips20 }}</el-button>
            </span>
        </el-dialog>

        <!-- Date Time Picker -->
        <el-dialog class="dialog-wrapper" :visible.sync="showDateTimePicker">
            <div class="dialog-title">{{ dialogTitle }}</div>
            <div class="dialog-title">Select New Expired Date and Time</div>
            <div class="dialog-body">
                <el-date-picker v-model="selectedDateTime" type="datetime"
                    value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
            </div>
            <div class="dialog-footer">
                <el-button @click="cancelDateTimeSelection">Cancel</el-button>
                <el-button type="primary" @click="confirmDateTimeSelection()">OK</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import timer from '../tools/timer';
import global from '../tools/global';
export default {
    name: 'team',
    data() {
        return {
            tableData: [
                { role: "Admin" },
                { role: "Designer" },
                { role: "Customer" }
            ],
            roles: ["Admin", "Designer", "Customer"],
            editRoleDialogVisible: false,
            editRoleForm: {
                role: ""
            },
            key: '',
            list: [],
            count: 0,
            page: 1,
            loading: false,
            showAdd: false,
            editingRow: null,
            selectedId: null,
            showDateTimePicker: false, // Track visibility of the DateTime picker
            selectedDateTime: "",

            selectedSubscription: '',
            addForm: {
                name: '',
                role: '2'
            }
        }
    },
    created() {
        this.lang = this.$lang('team');
    },
    mounted() {
        this.getlist();
    },
    computed: {
        currentExpiredTime() {
            return `<div class="dialog-title">this.current_expired_time = scope.expired_time;</div>`;
        }
    },
    methods: {
        // 获取数据列表
        getlist(page = 0) {
            if (page > 0) {
                this.page = page;
            }
            this.loading = true;
            this.$ajax.get(`/user/list/?page=${this.page}`).then(result => {
                console.log(result)
                const role = this.lang.role;
                const subscription = this.lang.subscription;
                this.count = Number(result.count);
                this.list = result.list.map(item => {
                    item.id = Number(item.id) + 1000000;
                    item.characterCount = item.characterCount;
                    item.last_time = timer.time('y-m-d h:i:s', item.last_time * 1000);
                    const timestamp = new Date(item.last_time);
                    const now = new Date();
                    const diff = Math.floor((now - timestamp) / 1000); // 时间差，单位为秒

                    if (diff < 60) {
                        item.last_time = diff + " seconds ago";
                    } else if (diff < 60 * 60) {
                        const minutes = Math.floor(diff / 60);
                        item.last_time = minutes + " minutes ago";
                    } else if (diff < 60 * 60 * 24) {
                        const hours = Math.floor(diff / (60 * 60));
                        item.last_time = hours + " hours ago";
                    } else if (diff < 60 * 60 * 24 * 30) {
                        const days = Math.floor(diff / (60 * 60 * 24));
                        item.last_time = days + " days ago";
                    } else if (diff < 60 * 60 * 24 * 30 * 12) {
                        const months = Math.floor(diff / (60 * 60 * 24 * 30));
                        item.last_time = months + " months ago";
                    } else {
                        const years = Math.floor(diff / (60 * 60 * 24 * 30 * 12));
                        item.last_time = years + " years ago";
                    }

                    item.create_time = timer.time('y-m-d', item.create_time * 1000);
                    item.statusText = item.status == 0 ? this.lang.status1 : this.lang.status2;
                    item.role = role[item.role];
                    item.subscription = subscription[item.subscription];
                    item.projectused = item.current_project + "/" + item.max_projects + " projects";
                    item.expired_time = item.expired_time;
                    item.avatar = `${global.host}${item.avatar}`;
                    return item;
                })
                this.loading = false;
            })
        },
        showLockConfirmation(id, status) {
            const user = this.list.find(item => item.id === id);
            const username = user.user_name;
            const action = status === 0 ? 'unlock' : 'lock';
            const confirmButtonText = status === 0 ? 'Yes, unlock' : 'Yes, lock';
            const cancelButtonText = 'No, cancel';
            const imageUrl = status === 0 ? require('@/assets/Definer_logo.png') : require('@/assets/Definer_logo.png');
            const lockstatus = status === 0 ? 'el-icon-unlock' : 'el-icon-lock';

            this.$confirm(`
    <div style="display: flex; justify-content: center; align-items: center; flex-direction: column;">
      <div style="display: flex; flex-direction: column; align-items: center;">
        <img src="${imageUrl}" style="width: 100px; height: auto; margin-bottom: 30px;" />
        <div style="background-color: #345CA0; padding: 15px; border-radius: 12px; margin-bottom: 30px;">
         <i class="${lockstatus}" style="font-size: 36px;color:white;"></i>
        </div>
        <p style="margin: 0; text-align: center; font-size: 16px; color: #333; margin-bottom: 20px; margin-left:50px; margin-right:50px;">
          Are you sure you want to ${action} account "${username}"?
        </p>
      </div>
    </div>
  `, {
                confirmButtonText,
                cancelButtonText,
                dangerouslyUseHTMLString: true,
                customClass: 'confirmation-box',
                distinguishCancelAndClose: true,
                closeOnClickModal: false,
                center: true, // Center the dialog
                showCancelButton: true, // Show the cancel button
                showClose: false, // Hide the close button
                confirmButtonClass: 'custom-confirm-button',
            }).then(() => {
                if (status === 0) {
                    // Handle unlock logic
                    this.off(id, status);
                } else {
                    // Handle lock logic
                    this.off(id, status);
                }
            }).catch(() => {
                // Confirmation canceled
            });
            const confirmButton = document.querySelector('.custom-confirm-button');
            if (confirmButton) {
                confirmButton.style.backgroundColor = '#345CA0';
            }
        },
        showDeleteConfirmation(id) {
            const user = this.list.find(item => item.id === id);
            const username = user.user_name;
            const confirmButtonText = 'Yes, delete';
            const cancelButtonText = 'No, cancel';
            const imageUrl = require('@/assets/Definer_logo.png');
            const lockstatus = 'el-icon-delete';
            const action = 'delete';

            this.$confirm(`
    <div style="display: flex; justify-content: center; align-items: center; flex-direction: column;">
      <div style="display: flex; flex-direction: column; align-items: center;">
        <img src="${imageUrl}" style="width: 100px; height: auto; margin-bottom: 30px;" />
        <div style="background-color: red; padding: 15px; border-radius: 12px; margin-bottom: 30px;">
         <i class="${lockstatus}" style="font-size: 36px;color:white;"></i>
        </div>
        <p style="margin: 0; text-align: center; font-size: 16px; color: #333; margin-bottom: 20px; margin-left:50px; margin-right:50px;">
          Are you sure you want to ${action} account "${username}"?
        </p>
      </div>
    </div>
  `, {
                confirmButtonText,
                cancelButtonText,
                dangerouslyUseHTMLString: true,
                customClass: 'confirmation-box',
                distinguishCancelAndClose: true,
                closeOnClickModal: false,
                center: true,
                showCancelButton: true,
                showClose: false,
                confirmButtonClass: 'custom-delete-button',
            }).then(() => {
                this.del(id);
            }).catch(() => {
                // Confirmation canceled
            });
            const confirmButton = document.querySelector('.custom-delete-button');
            if (confirmButton) {
                confirmButton.style.backgroundColor = 'red';
            }
        },
        // 锁定/解锁账户
        off(id, status) {
            this.$ajax.get(`/user/off/?id=${id - 1000000}&status=${status}`).then(result => {
                this.$message.success(this.lang.tips1);
                this.getlist();
            })
        },
        // 取消添加账户
        cancelAdd() {
            this.showAdd = false;
            this.addForm = {
                name: '',
                role: '2'
            }
        },
        // 删除
        del(id) {
            this.$ajax.get(`/user/del/?id=${id - 1000000}`).then(result => {
                this.$message.success(this.lang.tips6);
                this.getlist(1);
            })
        },
        // 添加账户
        add() {
            if (this.addForm.name == '') {
                this.$message.warning(this.lang.tips7);
                return;
            }
            this.$ajax.post('/user/add', {
                account: this.addForm.name,
                role: Number(this.addForm.role)
            }).then(result => {
                this.showAdd = false;
                this.addForm = {
                    name: '',
                    role: '2'
                }
                this.getlist(1);
            })
        },
        editMaxProjects(scope) {
            const newMaxProjects = parseInt(prompt('Enter the new max_projects value (1-5):'));
            const maxProjects = parseInt(newMaxProjects);

            if (isNaN(maxProjects) || maxProjects < 1 || maxProjects > 5) {
                alert('Invalid input! max_projects should be a number between 1 and 5.');
                return;
            }

            // Make a POST request to update the max_projects value
            this.$ajax.post('/project/updateMaxProjects', {
                id: scope.id,
                max_projects: maxProjects
            }).then(response => {
                // Handle the server response if needed
                console.log(response);
                scope.max_projects = maxProjects;
            }).catch(error => {
                // Handle the error if the request fails
                console.error(error);
            });
        },
        // Handle click event of the subscription button
        handleSubscriptionClick(scope) {
            // Show the DateTime picker
            this.showDateTimePicker = true;
            this.selectedId = scope.id;
            this.current_expired_time = scope.expired_time;
            this.dialogTitle = `Expired Time: ${this.current_expired_time}`;
        },

        // Handle cancel button click event of the DateTime picker
        cancelDateTimeSelection() {
            // Hide the DateTime picker
            this.showDateTimePicker = false;
            // Reset the selected date and time
            this.selectedDateTime = "";
        },

        // Handle OK button click event of the DateTime picker
        confirmDateTimeSelection() {
            if (this.selectedDateTime) {
                const newExpiration = this.selectedDateTime;

                this.$ajax.post('/project/updateExpiration', {
                    newExpiration, id: this.selectedId
                })
                    .then(result => {
                        // Handle the response
                        console.log(result);
                        this.$message.success('Expiration time updated successfully');
                    })
                    .catch(error => {
                        // Handle the error
                        console.error(error);
                        this.$message.error('Failed to update expiration time');
                    });

                // Close the dialog and reset values
                this.showDateTimePicker = false;
                this.selectedDateTime = "";
            }
        },

        handleSubscriptionChange(row) {
            this.$ajax.post('/project/updateSubscriptionChange', {
                subscription: row.subscription,
                id: row.id
            }).then(response => {
                // Handle the server response if needed
                console.log(response);
            }).catch(error => {
                // Handle the error if the request fails
                console.error(error);
            });
        },

        editRoleConfirmation(row) {
            this.editRoleForm.role = row.role;
            this.editRoleForm.id = row.id;
            this.editRoleDialogVisible = true;
        },
        cancelEditRole() {
            this.editRoleDialogVisible = false;
            this.$refs.editRoleForm.resetFields();
        },
        saveEditRole(row) {
            // Perform save operation here
            this.$ajax.post('/project/updateRole', {
                role: this.editRoleForm.role,
                id: this.editRoleForm.id
            }).then(response => {
                // Handle the server response if needed
                console.log(response);
                this.getlist();
            }).catch(error => {
                // Handle the error if the request fails
                console.error(error);
                this.getlist();
            });

            this.editRoleDialogVisible = false;
        }
    }
}
</script>

<style lang="less" scoped>
@import url('../tools/common.less');

.team {
    width: calc(100% - 60px);
    height: 100%;
    padding: 0 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    .head {
        width: calc(100% - 20px);
        height: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
        padding: 20px 10px;

        .left {
            display: flex;
            flex-direction: column;

            p {
                font-weight: 700;
                font-size: 24px;
                line-height: 29px;
                margin: 0;
            }

            p:last-child {
                font-weight: 400;
                font-size: 14px;
                color: #86868B;
                margin: 0;

                span {
                    font-weight: 700;
                    margin-left: 5px;
                }
            }
        }

        .right {
            display: flex;
            align-items: center;

            button {
                background: #345CA0;
                border-radius: 12px;
                padding: 15px 24px;
                border-width: 0px;
            }
        }
    }

    .table_wrapper {
        width: 100%;
        flex: 1;
        overflow-x: auto;
    }

    .editable-input {
        border: none;
        outline: none;
        background-color: transparent;
        width: 100%;
        padding: 0;
        margin: 0;
        font-size: inherit;
    }

    .table {
        width: 100%;
        height: 100%;

        img {
            width: 38px;
            height: 38px;
            vertical-align: middle;
            border-radius: 50%;
            margin-right: 10px;
        }

        .el-table__header-wrapper {
            background-color: #F5F5F7;

            .cell {
                font-weight: 700;
            }
        }

        .el-table__body-wrapper {
            .cell {
                font-weight: 400;
            }
        }

        .el-tooltip {
            .item {
                margin-left: 20px;
            }
        }
    }

    .pagelist {
        width: 100%;
        margin-top: 20px;
        display: flex;
        justify-content: flex-end;
    }

    // .dialog-footer {
    //     display: flex;
    //     justify-content: flex-end;
    // }
}

.dialog-wrapper {
    padding: 0px;
    margin: 0px;
    text-align: center;
}

.dialog-title {
    background-color: #409EFF;
    color: #fff;
    padding: 10px 0px 10px 0px;
}

.dialog-body {
    padding: 20px;
}
</style>